<template>
  <div>
    <!-- 顶部标题 -->
    <van-nav-bar title="黑马头条-登录" />
    <!-- 登录表单的vant组件 -->
    <div>
      <van-form @submit="onSubmit">
        <van-cell-group inset>
          <van-field v-model="user.mobile" name="mobile" label="手机号" placeholder="请输入11位手机号" required
            :rules="[{ required: true, message: '请填写11位正确的手机号', pattern: /^1[3-9]\d{9}$/ }]" />
          <van-field v-model="user.code" type="password" name="code" label="密码" placeholder="密码" required
            :rules="[{ required: true, message: '请填写6位密码', pattern: /^\d{6}$/ }]" />
        </van-cell-group>
        <div style="margin: 16px">
          <van-button round block :loading="isLoading" :disabled="isLoading" loading-text="正在登录..." type="primary"
            native-type="submit"> 登录
          </van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
// 按需导入登录接口
import { loginAPI } from '@/api/index'
// 调用vant反馈的函数组件
import { Notify } from 'vant'
// 导入封装的token处理模块
import { setToken } from '@/utils/token'

export default {
  name: 'Login',
  data() {
    return {
      user: {
        mobile: '18482638909', // 手机号
        code: '246810'// 密码
      },
      // loading属性值isLoading初始设为false(表示目前未加载，登录按钮加载效果不会出来，后续按照需求将其设为true即可)
      isLoading: false
    }
  },
  methods: {
    // 提交表单的方法,value参数可以接收表单提交的数据
    async onSubmit(values) {
      // 点击提交按钮后，isLoading设为true，按钮显示登录中，同时禁用按钮
      this.isLoading = true

      // 发起axios请求，并把请求数据传过去（并且捕获错误）
      try {
        const { data: res } = await loginAPI(this.user)
        console.log(res)
        Notify({ type: 'success', message: '登录成功' })
        // 调用保存token的方法，把后端返回的token值传过去
        setToken(res.data.token)
        // 保存refresh——token
        localStorage.setItem('refresh_token', res.data.refresh_token)

        // 最后进行跳转
        this.$router.replace({ path: '/layout/home' })
      } catch (err) {
        // 调用vant组件的Notify错误反馈函数
        Notify({ type: 'danger', message: '账号或密码错误' })
      }

      // 网络请求完成无论成功还是错误，都把状态关掉,让表单恢复正常使用
      this.isLoading = false
    }
  }
}
</script>

<style lang="less" scoped>
</style>
